import React, { Component, Fragment } from 'react';

import Navigation from '../components/Header/Navigation/Navigation';
import Logo from '../components/Header/Logo/Logo';
import Search from '../components/Header/Search/Search';
import TrendingMovies from '../components/Main/TrendingMovies/TrendingMovies';

import axiosRequests from '../Requests/Requests';

import Spinner from '../components/Main/Spinner/Spinner';

import Pagination from 'react-pagination-js';
import 'react-pagination-js/dist/styles.css';

//CSS========================
import styles from '../components/Header/Navigation/Navigation.module.css';
import stylesPagination from '../components/Main/Pagination/Pagination.module.css';
import stylesMain from '../components/Main/Main.module.css';

export default class home extends Component {
    state = {
        galleryMovies: [],
        loading: true,
        width: window.innerWidth,
    };

    configuration = {};

    pagesData = { page: 1, total_pages: 0 };

    componentDidMount() {
        localStorage.setItem('search', '');
        this.getConfiguration();
        this.loadGalleryMovies();
    }

    updSize = () => {
        this.setState({
            width: window.innerWidth,
        });
    };

    componentDidUpdate() {
        window.addEventListener('resize', this.updSize);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.updSize);
    }

    getConfiguration() {
        return axiosRequests
            .axiosGetConfiguration()
            .then(request => (this.configuration = request.data.images));
    }

    scroll = () => {
        window.scrollTo({
            top: 0,
            behavior: 'smooth',
        });
    };

    //загрузка галереи фильмов через поиск
    loadSearchMovies = (inputValue, searchPage) => {
        this.setState({ loading: true });
        this.pagesData.page = searchPage;
        axiosRequests
            .axiosSearchRequest(inputValue, searchPage)
            .then(request => {
                this.pagesData.total_pages = request.data.total_pages;
                this.setState(() => {
                    return {
                        galleryMovies: request.data.results,
                    };
                });
            })
            .finally(() => {
                this.setState({ loading: false });
            });
        this.scroll();
    };

    //загрузка главной галереи фильмов
    loadGalleryMovies() {
        this.setState({ loading: true });
        axiosRequests
            .axiosLoadTrend(this.pagesData.page)
            .then(request => {
                this.pagesData.total_pages = request.data.total_pages;
                this.setState(() => {
                    return {
                        galleryMovies: request.data.results,
                    };
                });
            })
            .finally(() => {
                this.setState({ loading: false });
            });
        this.scroll();
    }

    //после пагинации
    changeCurrentPage = numPage => {
        this.pagesData.page = numPage;

        let stringSearch = localStorage.getItem('search');
        stringSearch
            ? this.loadSearchMovies(stringSearch, this.pagesData.page)
            : this.loadGalleryMovies();
    };

    render() {
        let { galleryMovies, width, loading } = this.state;
        console.log(this.state);

        return (
            <Fragment>
                <header>
                    <div className={styles.wrapHeader}>
                        <Logo />
                        <Navigation
                            onWidth={width}
                            onConfiguration={this.configuration}
                        />
                        <Search onRenderSearchRequest={this.loadSearchMovies} />
                    </div>
                </header>
                <main>
                    <div className={stylesMain.wrapMain}>
                        {!loading && (
                            <TrendingMovies
                                arrMovies={galleryMovies}
                                configuration={this.configuration}
                                onWidth={width}
                            />
                        )}
                        {loading && <Spinner />}
                        <div className={stylesPagination.pagination_box}>
                            <Pagination
                                currentPage={this.pagesData.page}
                                //тут глюк - теряется последняя цифра
                                totalSize={this.pagesData.total_pages * 10}
                                sizePerPage={10}
                                changeCurrentPage={this.changeCurrentPage}
                                theme="bootstrap"
                            />
                        </div>
                    </div>
                </main>
                <footer></footer>
            </Fragment>
        );
    }
}
