import React from 'react';
import styles from './TrendingMovies.module.css';

let TrendingMoviesItem = ({
    id,
    poster_path,
    title,
    popularity,
    release_date,
    configuration,
}) => {
    title = title.length > 26 ? title.slice(0, 26) + '...' : title;
    poster_path = poster_path
        ? configuration.base_url + configuration.poster_sizes[4] + poster_path
        : 'https://image.freepik.com/free-vector/man-sitting-on-big-question-mark-symbol_24381-1192.jpg';

    let LSQ = () => {
        let titleArr = JSON.parse(localStorage.getItem('queue')) || [];
        titleArr.push({ id, poster_path, title, popularity, release_date });
        localStorage.setItem('queue', JSON.stringify(titleArr));
    };

    let LSWatched = () => {
        let titleArr = JSON.parse(localStorage.getItem('watched')) || [];
        titleArr.push({ id, poster_path, title, popularity, release_date });
        localStorage.setItem('watched', JSON.stringify(titleArr));
    };

    return (
        <li className={styles.mainLI}>
            <div className={styles.containerIMG}>
                <button className={styles.svgBtn} onClick={LSQ}>
                    <svg className={styles.queueBTN}>
                        <path d="M17 3H7c-1.1 0-2 .9-2 2v16l7-3 7 3V5c0-1.1-.9-2-2-2zm0 15l-5-2.18L7 18V6c0-.55.45-1 1-1h8c.55 0 1 .45 1 1v12z"></path>
                    </svg>
                </button>
                <a href={''}>
                    <img
                        className={styles.mainIMG}
                        src={poster_path}
                        alt={title}
                    />
                </a>
            </div>
            <p className={styles.title}>
                <a onClick={LSWatched} href={''}>
                    {title}
                </a>
            </p>
            <p>Popularity: {popularity}</p>
            <p>Release: {release_date}</p>
        </li>
    );
};

export default TrendingMoviesItem;
